                var unique = [...new Set(res.map((item) => item['username']))];
                unique=unique.map(e=>{
                    return {
                        username:e
                    }
                })
                unique.forEach((e,i)=>{ // [{id:1,status:'masuk'},{id:2,status:'keluar'}]
                    unique[i].data=[]; // [{id:1,status:'masuk',data:[]},{id:2,status:'keluar',data:[]}]
